import React from 'react';
import { Button, ListGroup } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function QuiSuisJe() {


    function HeaderList(){
        return (
          <ListGroup as={"ul"}>
            <ListGroup.Item as={"li"}><Link to={"/#top"}><Button variant='link'>Accueil</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"} id='apropos'>
              <Button variant='link'>A propos...</Button>
              <ListGroup as={"ul"} className='submenu'>
                <ListGroup.Item as={"li"} active={true}><Button variant='link' href='#top'>Qui suis-je ?</Button></ListGroup.Item>
                <ListGroup.Item as={"li"}><Link to={"/bureau"}><Button variant='link'>Consultation</Button></Link></ListGroup.Item>
              </ListGroup>
            </ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/formation"}><Button variant='link'>Formation</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/#formManager"}><Button variant='link'>Contactez-nous</Button></Link></ListGroup.Item>
          </ListGroup>
        )
    }

    function FooterComponent(){
        return (
          <footer>
            <div className='left'>
              <a href="#top"><Button variant='link'>Entreprise</Button></a>
              <Link to={"/avis"}><Button variant='link'>Avis des clients</Button></Link>
              <a href='#top'><Button variant='link'>A propos</Button></a>
            </div>
            <div className='middle'>
              <Link to={"/formation"}><Button variant='link'>Formation</Button></Link>
              <Link to={"/blog"}><Button variant='link'>Blog</Button></Link>
              <Link to={"/rgpd"}><Button variant='link'>RGPD</Button></Link>
            </div>
            <div className='right'>
            <Link to={"/cgv"}><Button variant='link'>Conditions</Button></Link>
              <Link to={"/politique"}><Button variant='link'>Politiques</Button></Link>
              <Link to={"/cookies"}><Button variant='link'>Paramètres de cookies</Button></Link>
            </div>
          </footer>
        )
      }

    return (<div className='qui-suis-je App'>
        <div id='top'></div>
        <header>
            <div className='HeaderTop'>
            <h1>Scarabée Doré</h1>
            </div>
            <div className='HeaderBottom'>
            <HeaderList/>
            </div>
        </header>
        <main>
            <h1>Qui suis-je ?</h1>
            <section id="part-1">
                <span>Nom: Sandra DUARTE</span>
                <span>Profession: Coach de vie professionnelle et personnelle</span>
                <p className="mt-3">Bonjour, je m'appelle Sandra et je suis coach professionnelle.</p>
                <span className="mt-3">C'est quoi un coach de vie professionnel ?</span>
                <p className="mt-3">Un coach aide la personne à définir et à clarifier  un objectif, ainsi que la mise en place des mesures appropriées et des étapes nécessaires  permettant l’atteinte de cet objectif lui permettant de se sentir mieux et épanoui.</p>
                <span className="mt-3">Quelle est la différence entre un thérapeute et un coach de vie ?</span>
                <p className="mt-3">Le thérapeute est un professionnel ayant un master en psychologie (après une licence du même domaine) ou en psychanalyse et ayant suivi une formation en psychopathologie clinique où un doctorat de médecine avec une spécialité en psychiatrie.  Il soigne les blessures du passé.

Concernant les coachs, pour exercer en France, il n’est pas obligatoire de suivre une formation ni d’obtenir un diplôme ou une certification. Le coach  intervient sur des problèmes concrets (relationnel, reconversion professionnelle, confiance en soi….), qui ont été définis avec le client au préalable lors du premier entretien.  Le coach de vie ne diagnostique pas ni ne soigne les maladies mentales</p>
                <span className="mt-3">Ma philosophie et mes valeurs</span>
                <p className="mt-3">« Si on veut obtenir quelque chose que l’on n’a jamais eu, il faut tenter quelque chose que l’on n’a jamais fait. » Périclès.<br/><br/>
                  Nous avons parfois besoin d’une tierce personne pour nous permettre de prendre le recul nécessaire afin d’atteindre le ou les objectifs…Dans ma vie, j’ai toujours pris le temps d’analyser mes situations en profondeur, avec le “positif” et le “négatif” des choix…car oui la prise de décision qu’on fera aura toujours un impact positif et négatif sur notre vie. Pour cela nous devons prendre en considération tous les paramètres et prendre la décision qui nous paraît être la meilleure (plus de positif que de négatif) et surtout de s’y tenir.
                  <br/><br/>Je suis une personne empathique, honnête, de confiance,  ayant une bonne capacité d’adaptation à la personne et à la situation, une ouverture d’esprit.</p>
            </section>
        </main>
        <FooterComponent/>
    </div>);
}

export default QuiSuisJe;